// Import Bootstrap variables, mixins, utilities
@import "../../bower_components/bootstrap/less/variables.less";
@import "../../bower_components/bootstrap/less/mixins.less";

// Core variables and mixins
@import "../variables.less";
@import "../mixins.less";

body {
  position: relative;
}

.navbar-toggle .icon-bar {
  background-color: @gray-dark;
}

a, img {outline: none !important;}

.wf-notos-n3-active,
.wf-notos-n4-active,
.wf-notos-n7-active {
  body {
    font-family: Verdana, Roboto, 'Droid Sans', NoToS, sans-serif;
    -ms-font-feature-settings: 'kern' 1;
    -moz-font-feature-setting: 'kern=1';
  }
  &.wf-opensans-n3-active,
  &.wf-opensans-n4-active,
  &.wf-opensans-n7-active {
    body {font-family: 'Open Sans', NoToS, sans-serif;}
  }
}

.font-loading {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  font-size: 0;
  list-style: none;
  background-color: transparent;
  .wf-active & {
    .transition(~'background-color .5s linear 1s, z-index .001s linear 1.501s');
  }
  .wf-processing & {
    z-index: 10010;
    background-color: #fff;
  }
  > li {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 10020;
    border: @font-size-base solid transparent;
    border-radius: 100%;
    &:first-child {
      margin-top: (@font-size-base * -2);
      margin-left: (@font-size-base * -2);
      width: (@font-size-base * 4);
      height: (@font-size-base * 4);
      border-top-color: @brand-danger;
      border-right-color: @brand-danger;
      .animation(spin-r 1s linear infinite);
    }
    &:last-child {
      margin-top: (@font-size-base * -3);
      margin-left: (@font-size-base * -3);
      width: (@font-size-base * 6);
      height: (@font-size-base * 6);
      border-bottom-color: @brand-info;
      border-left-color: @brand-info;
      .animation(spin-l 1.5s linear infinite);
      .wf-active & {
        .animation(spin-l 1s linear infinite);
      }
    }
    transform: scale(.9999);
    .wf-active & {
      margin-top: -@font-size-base;
      margin-left: -@font-size-base;
      width: 0;
      height: 0;
      border-color: @brand-success;
      .transition(~'margin-left .5s linear .5s, margin-top .5s linear, width .5s linear .5s, height .5s linear, border-color .5s ease-in');
    }
  }
}
@keyframes blink {
  0% {.opacity(0);}
  50% {.opacity(1);}
  100% {.opacity(0);}
}
@keyframes spin-r {
  0% {transform: rotate(45deg);}
  100% {transform: rotate(405deg);}
}
@keyframes spin-l {
  0% {transform: rotate(405deg);}
  100% {transform: rotate(45deg);}
}

#fullpage-nav {
  li a {color: @gray-light;}
  li.active a {
    color: @gray-lighter;
    display: block;
    &:before,
    &:after {
      position: absolute;
      top: (floor((@font-size-base / 2)) + 1);
      display: inline-block;
      font-size: @font-size-large;
      font-weight: 700;
      color: inherit;
      .opacity(.75);
      @media (min-width: @screen-md-min) {
        top: (@font-size-base - 1);
      }
    }
    &:before {
      left: floor((@font-size-base / 2));
      content: '\007b';
    }
    &:after {
      content: '\007d';
    }
  }
}

.section {
  text-align: center;
  p {
    @media (min-width: @screen-md-min) {
      font-size: @font-size-h2;
    }
  }
  .heading {
    @media (min-width: @screen-md-min) {
      font-size: (@font-size-h1 * 2);
    }
  }
  .checkbox label {
    text-align: left;
  }
}
